___CSS_LOADER_EXPORT___.push([module.id, `:root {
  /* General */
  --small-screen-max: 340px;
  --medium-screen-min: 768px;
  --large-screen-min: 1024px;
  --padding: 5%;
  --gap: 64px;
  --line-width: 2px;
  --border-radius-small: 4px;
  --border-radius-medium: 10px;
  --border-radius-large: 20px;

  /* Colors */
  --clr-primary-red: hsl(3, 58%, 28%);
  --clr-primary-yellow: hsl(47, 100%, 78%);
  --clr-primary-blue: hsl(185, 76%, 64%);

  --clr-accent-900: hsla(0, 0%, 0%, 0.2);

  --clr-neutral-100: hsl(0, 0%, 100%); /* #FFFFFF */
  --clr-neutral-400: hsl(33, 100%, 98%); /* #FFF9F1 */
  --clr-neutral-900: hsl(0, 0%, 0%); /* #000000 */

  /* Fonts */
  --ff-title: "EB Garamond", serif;
  --ff-heading: "EB Garamond", serif;
  --ff-body: "Open Sans", sans-serif;

  --fw-regular: 400;
  --fw-medium: 500;
  --fw-semi-bold: 600;
  --fw-bold: 700;

  --fs-300: 0.875rem; /* 14px */
  --fs-400: 1rem; /* 16px */
  --fs-500: 1.25rem; /* 20px */
  --fs-600: 1.5rem; /* 24px */
  --fs-700: 2rem; /* 32px */
  --fs-750: 2.25rem; /* 36px */
  --fs-800: 2.5rem; /* 40px */
  --fs-850: 3rem; /* 48px */
  --fs-900: 4rem; /* 64px */

  --fs-title: var(--fs-700);
  --fs-primary-heading: var(--fs-600);
  --fs-secondary-heading: var(--fs-600);
  --fs-body: var(--fs-400);
  --fs-body-large: var(--fs-500);
  --fs-nav: var(--fs-700);
  --fs-small-button: var(--fs-300);
  --fs-medium-button: var(--fs-300);
  --fs-large-button: var(--fs-500);

  /* Sizes */
  --size-100: 4px;
  --size-200: 8px;
  --size-300: 12px;
  --size-400: 16px;
  --size-500: 24px;
  --size-600: 40px;
  --size-700: 80px;
  --size-800: 120px;
  --size-900: 160px;
}

@media (min-width: 560px) {
  :root {
    --fs-title: var(--fs-900);
    --fs-primary-heading: var(--fs-700);
    --fs-secondary-heading: var(--fs-600);
    --fs-body: var(--fs-500);
    --fs-body-large: var(--fs-600);
  }
}

@media (min-width: 768px) {
  :root {
    --fs-nav: var(--fs-500);
    --fs-small-button: var(--fs-300);
    --fs-medium-button: var(--fs-400);
    --fs-large-button: var(--fs-500);
  }
}
