.not-clickable {
  pointer-events: none;
}

/* Accessability */
.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

/* Columns */
@media (min-width: 1024px) {
  .even-columns,
